.taroify-space__item {
  display: flex;
}
.taroify-space__item--mini {
  margin-bottom: var(--space-item-vertical-gap-mini, var(--space-item-gap-mini, var(--padding-xs, 16px)));
}
.taroify-space__item--small {
  margin-bottom: var(--space-item-vertical-gap-small, var(--space-item-gap-small, var(--padding-sm, 24px)));
}
.taroify-space__item--medium {
  margin-bottom: var(--space-item-vertical-gap-medium, var(--space-item-gap-medium, var(--padding-md, 32px)));
}
.taroify-space__item--large {
  margin-bottom: var(--space-item-vertical-gap-large, var(--space-item-gap-large, var(--padding-lg, 48px)));
}
.taroify-space__item--fill {
  width: 100%;
}
.taroify-space--horizontal .taroify-space__item--mini {
  margin-right: var(--space-item-horizontal-gap-mini, var(--space-item-gap-mini, var(--padding-xs, 16px)));
}
.taroify-space--horizontal .taroify-space__item--small {
  margin-right: var(--space-item-horizontal-gap-small, var(--space-item-gap-small, var(--padding-sm, 24px)));
}
.taroify-space--horizontal .taroify-space__item--medium {
  margin-right: var(--space-item-horizontal-gap-medium, var(--space-item-gap-medium, var(--padding-md, 32px)));
}
.taroify-space--horizontal .taroify-space__item--large {
  margin-right: var(--space-item-horizontal-gap-large, var(--space-item-gap-large, var(--padding-lg, 48px)));
}