.taroify-flex {
  display: flex;
}
.taroify-flex::before, .taroify-flex::after {
  display: flex;
}
.taroify-flex--justify-start {
  justify-content: flex-start;
}
.taroify-flex--justify-center {
  justify-content: center;
}
.taroify-flex--justify-end {
  justify-content: flex-end;
}
.taroify-flex--justify-space-between {
  justify-content: space-between;
}
.taroify-flex--justify-space-around {
  justify-content: space-around;
}
.taroify-flex--align-start {
  align-items: flex-start;
}
.taroify-flex--align-center {
  align-items: center;
}
.taroify-flex--align-end {
  align-items: flex-end;
}
.taroify-flex--align-baseline {
  align-items: baseline;
}
.taroify-flex--align-stretch {
  align-items: stretch;
}
.taroify-flex--row {
  flex-direction: row;
}
.taroify-flex--row-reverse {
  flex-direction: row-reverse;
}
.taroify-flex--column {
  flex-direction: column;
}
.taroify-flex--column-reverse {
  flex-direction: column-reverse;
}
.taroify-flex--nowrap {
  flex-wrap: nowrap;
}
.taroify-flex--wrap {
  flex-wrap: wrap;
}
.taroify-flex--wrap-reverse {
  flex-wrap: wrap-reverse;
}

